.intro {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    position: relative;
    margin-top: -44px;
    margin-bottom: 68px;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 23px;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat no-repeat;
      background-size: cover;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 115px;

    &::after {
      height: 34px;
    }
  }
}

.intro--index {
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center -17px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 55px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: top;

    &::after {
      bottom: -14px;
      right: 6px;
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 59px;
    background-image: url("../img/hero-desktop@1x.jpg");
    background-color: #f9f9f9;

    &::after {
      bottom: -3px;
      right: 0;
      background-image: url("../img/zigzag-fill-desktop.svg");
    }

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }
  }
}

.intro--form-page {
  margin-bottom: 18px;

  @media (min-width: $tablet-width) {
    margin-bottom: 56px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 102px;
  }
}

.intro__title {
  padding: 73px 30px;
  margin: 0;
  text-align: center;
  font-size: 27px;
  line-height: 30px;
  font-weight: bold;
  background-color: $lightgrey;

  @media (min-width: $tablet-width) {
    padding: 102px 50px 85px;
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 110px;
    font-size: 53px;
    line-height: 57px;
  }

  &--index {
    padding-bottom: 120px;
    background-color: transparent;

    @media (min-width: $tablet-width) {
      padding-top: 155px;
      padding-bottom: 488px;
    }
  }
}
