.header {
  @media (min-width: $tablet-width) {
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      top: 88px;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }
}

.header__top {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    z-index: 5;
    position: relative;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.header__logo {
  order: -1;
  height: 79px;
  text-align: center;
  box-shadow: 0 1px 0 $bordercolor, inset 0 1px 0 $bordercolor;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 5px;
    box-shadow: none;
  }

  @media (min-width: $desktop-width) {
    height: 72px;
    right: 43%;
  }
}

.header__logo img {
  margin-top: 22px;
  width: 86px;
  height: 35px;

  &:hover {
    opacity: 0.6;
  }

  &:focus,
  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin-top: 17px;
    width: 113px;
    height: auto;
  }

  @media (min-width: $desktop-width) {
    margin-top: 4px;
    width: 149px;
  }
}
